import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {Link as RouterLink} from "react-router-dom";
import Web3 from "web3";

import LITRaffle from "../../abis/LITRaffle.json";
import IERC20 from "../../abis/IERC20.json";

import {
    Avatar,
    Button,
    Container,
    Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper, Skeleton,
    ToggleButton,
    ToggleButtonGroup,
    Typography
} from "@mui/material";

import {makeBatchRequest} from "../../utils/promisify";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../../utils/currency_format";

import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import CurrencyBitcoinIcon from '@mui/icons-material/CurrencyBitcoin';

export default function Raffles() {

    const [displayUSD, setDisplayUSD] = useState(false);

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
                <Grid item xs={12} style={{textAlign: 'right'}}>
                    <ToggleButtonGroup
                        value={displayUSD}
                        exclusive
                        onChange={() => setDisplayUSD(!displayUSD)}
                        aria-label="text alignment"
                        color="primary"
                    >
                        <ToggleButton value={false}>
                            <CurrencyBitcoinIcon/>
                        </ToggleButton>
                        <ToggleButton value={true}>
                            <AttachMoneyIcon/>
                        </ToggleButton>
                    </ToggleButtonGroup>
                </Grid>

                <RaffleGameInfo
                    contract_address='0x146d3e377d1e8B4aFdeeCAb3Adb050f66D230b0b'
                    displayUSD={displayUSD}
                    network="fuse"
                />

                {/*<RaffleGameInfo*/}
                {/*    contract_address='0x8C9F410BE201c6e1d3347A598A75331d7c937dD6'*/}
                {/*    displayUSD={displayUSD}*/}
                {/*    network="fuse"*/}
                {/*/>*/}

            </Grid>
        </Container>
    );
}

function RaffleGameInfo(props) {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const {contract_address, network, displayUSD} = props;

    const [raffle, setRaffle] = useState({nextDraw: 0});

    const getRaffleGameInfo = () => {

        const web3 = new Web3(rpc[network]);

        const raffle_contract = new web3.eth.Contract(LITRaffle, contract_address);

        const calls = [
            raffle_contract.methods.ticketCost().call,
            raffle_contract.methods.adminFee().call,
            raffle_contract.methods.raffleID().call,
            raffle_contract.methods.entriesAllowed().call,
            raffle_contract.methods.nextDraw().call,
            raffle_contract.methods.nextDrawIntervalHours().call,
            raffle_contract.methods.totalFees().call,
            raffle_contract.methods.totalWinnings().call,
            raffle_contract.methods.getTokens().call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            const [
                ticketCost,
                adminFee,
                raffleID,
                entriesAllowed,
                nextDraw,
                nextDrawInterval,
                totalFees,
                totalWinnings,
                tokens
            ] = resp;

            const token_contract = new web3.eth.Contract(IERC20, tokens.jackpot.id);

            token_contract.methods.balanceOf(contract_address).call((err, resp) => {

                const jackpot = tokens.entry.id === tokens.jackpot.id ? (resp - (resp / adminFee)) / (10 ** 18) : resp/(10**18);

                setRaffle({
                    ticketCost: ticketCost / (10 ** 18),
                    adminFee,
                    raffleID,
                    entriesAllowed,
                    nextDraw: nextDraw * 1000,
                    nextDrawInterval,
                    totalFees,
                    totalWinnings,
                    tokens,
                    jackpot
                });
            })
        })

    }

    useEffect(() => {
        getRaffleGameInfo();
        const interval = setInterval(getRaffleGameInfo, 10000)
        return () => clearInterval(interval);
    }, []);

    const [drawingTime, setDrawingTime] = useState(undefined);
    useEffect(() => {
        setDrawingTime(msToHMS(timeToNextDraw(raffle.nextDraw)));
        const interval1 = setInterval(() => {
            setDrawingTime(msToHMS(timeToNextDraw(raffle.nextDraw)));
        }, 1000);
        return () => clearInterval(interval1);
    }, [raffle]);

    if (Object.entries(raffle).length === 1) {
        return <RaffleDetailsLoading />;
    }

    return <Grid item xs={12}>
        <Paper>
            {
                (timeToNextDraw(raffle.nextDraw) > 0) ?
                    <Typography variant="caption" color="primary" style={{float: "left", padding: '15px 15px 0 15px'}}>Drawing
                        in {drawingTime}</Typography> :
                    <Typography variant="caption" color="primary" style={{float: "left", padding: '15px 15px 0 15px'}}>Waiting
                        for Winner</Typography>
            }
            <Typography variant="caption" color="primary"
                        style={{float: "right", padding: 15}}>Max Entries: {raffle.entriesAllowed} -
                Draw# {raffle.raffleID}</Typography>
            <List>
                <ListItem>
                    <Grid container spacing={2}>
                        <Grid item xs={6} style={{textAlign:'center'}}>
                            <ListItemAvatar>
                                <Avatar src={`https://coineus.app/assets/tokens/${network}/${raffle.tokens.entry.id}/logo.png`} style={{margin:'0 auto'}}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary={displayUSD ? `~${CoineusUSDFormat(raffle.ticketCost * (wallet.prices[network][raffle.tokens.entry.id]?.inUSD || 0))}` : `${CoineusCryptoFormat(raffle.ticketCost)} ${raffle.tokens.entry.symbol}`}
                                primaryTypographyProps={{
                                    style: {fontWeight: 700},
                                    variant: 'h5'
                                }}
                                secondary={`Ticket Price`}
                            />
                        </Grid>
                        <Grid item xs={6} style={{textAlign:'center'}}>
                            <ListItemAvatar>
                                <Avatar src={`https://coineus.app/assets/tokens/${network}/${raffle.tokens.jackpot.id}/logo.png`} style={{margin:'0 auto'}}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary={displayUSD ? `~${CoineusUSDFormat(raffle.jackpot * (wallet.prices[network][raffle.tokens.jackpot.id]?.inUSD || 0))}` : `${CoineusCryptoFormat(raffle.jackpot)} ${raffle.tokens.jackpot.symbol}`}
                                primaryTypographyProps={{
                                    style: {fontWeight: 700},
                                    variant: 'h5'
                                }}
                                secondary={`Current Jackpot`}
                            />
                        </Grid>
                    </Grid>

                </ListItem>

                <ListItem>
                    <RouterLink to={`/raffles/${network}/${contract_address}`}
                                style={{width: '100%', textDecoration: 'none'}}>
                        <Button
                            fullWidth
                            variant="contained"
                        >Enter</Button>
                    </RouterLink>
                </ListItem>
            </List>
        </Paper>
    </Grid>
}

function RaffleDetailsLoading() {
    return (
        <Grid item xs={12}>
            <Paper style={{padding: 15}}>
                <Skeleton style={{marginBottom:10}}/>
                <Grid container>
                    <Grid item><Skeleton variant="circular" width={40} height={40}/></Grid>
                    <Grid item style={{flex:1, marginLeft:10}}>
                        <Skeleton variant="rounded" height={24} width="100%" style={{marginBottom:5}}/>
                        <Skeleton variant="rounded" height={11} width="100%"/>
                    </Grid>
                </Grid>

                <Skeleton height={60}/>
            </Paper>
        </Grid>);
}

function timeToNextDraw(d) {
    return d - new Date();
}

function msToHMS(ms) {
    let h = ms / 3.6e6 | 0;
    let m = (ms % 3.6e6) / 6e4 | 0;
    let s = (ms % 6e4) / 1e3 | 0;
    return `${h}:${('' + m).padStart(2, '0')}:${('' + s).padStart(2, '0')}`;
}