import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import Web3 from "web3";
import {
    Button,
    Container,
    Fab,
    Grid,
    List,
    ListItem,
    ListItemText,
    Paper,
    Skeleton,
    Typography
} from "@mui/material";
import {makeBatchRequest} from "../../utils/promisify";
import {CoineusCryptoFormat} from "../../utils/currency_format";
import CoineusLoading from "../../components/CoineusLoading";
import {useParams, Link as RouterLink} from "react-router-dom";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

import LITRaffle from "../../abis/LITRaffle.json";
import IERC20 from "../../abis/IERC20.json";

import {toWei} from "../../utils/format";

export default function RaffleGame() {

    let {contract_address, network} = useParams();

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [pending, setPending] = useState(false);

    const [raffle, setRaffle] = useState({});

    const getRaffleInfo = (contract_address, callback) => {

        if(!wallet.address)return;

        const web3 = new Web3(rpc[network]);

        const raffle_contract = new web3.eth.Contract(LITRaffle, contract_address);

        const calls = [
            raffle_contract.methods.ticketCost().call,
            raffle_contract.methods.adminFee().call,
            raffle_contract.methods.raffleID().call,
            raffle_contract.methods.entriesAllowed().call,
            raffle_contract.methods.nextDraw().call,
            raffle_contract.methods.nextDrawIntervalHours().call,
            raffle_contract.methods.totalFees().call,
            raffle_contract.methods.totalWinnings().call,
            raffle_contract.methods.getPlayers().call,
            raffle_contract.methods.getStatsByAddress(wallet.address).call,
            raffle_contract.methods.getTokens().call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            const [
                ticketCost,
                adminFee,
                raffleID,
                entriesAllowed,
                nextDraw,
                nextDrawInterval,
                totalFees,
                totalWinnings,
                players,
                playerStats,
                tokens
            ] = resp;

            const token_contract = new web3.eth.Contract(IERC20, tokens.jackpot.id);

            const calls2 = [
                token_contract.methods.balanceOf(contract_address).call,
                raffle_contract.methods.getGameHistoryByRaffleID(raffleID-1).call
            ];

            makeBatchRequest(web3, calls2).then( resp => {
                const [
                    jackpot,
                    history
                ] = resp;

                callback({
                    ticketCost: ticketCost / (10 ** 18),
                    adminFee,
                    raffleID,
                    entriesAllowed: parseInt(entriesAllowed),
                    nextDraw: nextDraw * 1000,
                    nextDrawInterval,
                    totalFees,
                    totalWinnings: totalWinnings / (10**18),
                    jackpot: tokens.entry.id === tokens.jackpot.id ? (jackpot - (jackpot / adminFee)) / (10 ** 18) : jackpot/(10**18),
                    players,
                    previousWinner: history.winner,
                    previousJackpot: history.jackpot,
                    playerStats,
                    tokens
                });
            });
        })
    }

    useEffect(() => {
        getRaffleInfo(contract_address, setRaffle);
        const interval = setInterval(() => getRaffleInfo(contract_address, setRaffle), 10000)
        return () => clearInterval(interval);
    }, [rpc[network], wallet.address]);

    const [drawingTime, setDrawingTime] = useState(0);
    useEffect(() => {
        setDrawingTime(msToHMS(timeToNextDraw(raffle.nextDraw)));
        const interval = setInterval(() => {
            setDrawingTime(msToHMS(timeToNextDraw(raffle.nextDraw)));
        }, 1000);
        return () => clearInterval(interval);
    })

    const pickWinner = () => {
        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(LITRaffle, contract_address);
        contract.methods.pickWinner("coineus" + (new Date()).getTime())
            .send(
                {
                    from: wallet.address
                }
            )
            .then(function (receipt) {
                getRaffleInfo()
            })
            .catch(e=>console.log);

    }

    const enter = async () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const entry_contract = new web3.eth.Contract(IERC20, raffle.tokens.entry.id);
            const raffle_contract = new web3.eth.Contract(LITRaffle, contract_address);

            entry_contract
                .methods
                .allowance(wallet.address, contract_address)
                .call((err, resp) => {

                    console.log(resp);

                    if (err) return;

                    if ((resp / (10 ** 18)) < raffle.ticketCost) {
                        entry_contract
                            .methods
                            .approve(contract_address, toWei(10000))
                            .send({from: wallet.address, gasPrice: 10000000000})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                deposit()
                            });
                    } else {
                        deposit()
                    }
                })

            const deposit = () => {
                raffle_contract.methods.enter()
                    .send({from: wallet.address, gasPrice: 10000000000})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        getRaffleInfo(contract_address, setRaffle)
                        setPending(false);
                    });
            }
        }
    }

    const checkChain = () => {
        switch (network) {
            case "avax":
                if (wallet.chain === 43114) return true;
                break;
            case "bnb":
                if (wallet.chain === 56) return true;
                break;
            case "fuse":
                if (wallet.chain === 122) return true;
                break;
            default:
                return false;
        }
    }

    const changeNetwork = async () => {
        let chainID = 122;
        switch (network) {
            case "avax":
                chainID = 43114;
                break;
            case "bnb":
                chainID = 56;
                break;
            case "fuse":
                chainID = 122;
                break;
            default:
                return false;
        }

        const web3 = new Web3(window.provider);
        await window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{chainId: web3.utils.toHex(chainID)}], // chainId must be in hexadecimal numbers
        });
    }

    const getButtonLabel = () => {
        if (raffle.raffleState === 1)
            return "RAFFLE CLOSED";

        if (getOccurrence(raffle.players, wallet.address) >= raffle.entriesAllowed)
            return "MAX ENTRIES";

        if (timeToNextDraw(raffle.nextDraw) < 0)
            return "WAITING TO PICK WINNER";

        return `BUY TICKET (${raffle.ticketCost} ${raffle.tokens.entry.symbol})`;
    }

    const getButtonDisabled = () => {
        if (raffle.raffleState === 1)
            return true;

        if (getOccurrence(raffle.players, wallet.address) >= raffle.entriesAllowed)
            return true;

        return timeToNextDraw(raffle.nextDraw) < 0;
    }

    if (Object.entries(raffle).length < 1) {
        return <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
                <Grid item xs={12}>
                    <Paper style={{padding:15}}>
                        <Skeleton />
                    </Paper>
                </Grid>
            </Grid>
        </Container>;
    }

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
                <Grid item xs={12}>
                    <Fab to={"/raffles"} component={RouterLink} color="primary" size="small" style={{position:'fixed', top: 8, left: 8, zIndex:99999}}><ArrowBackIcon /></Fab>
                </Grid>

                <Grid item xs={12}>
                    <Paper style={{border: '1px solid #fbca00'}}>
                        <List>
                            <ListItem>
                                <ListItemText primary={`${CoineusCryptoFormat(raffle.totalWinnings)} ${raffle.tokens.jackpot.symbol}`}
                                              primaryTypographyProps={{
                                                  style: {fontWeight: 700},
                                                  variant: 'h5',
                                                  color: 'primary',
                                                  textAlign: 'center'
                                              }}
                                              secondary="Total Winnings All-time"
                                              secondaryTypographyProps={{textAlign: 'center'}}
                                />
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                {
                    raffle.ticketCost === 0 && <Grid item xs={12}>
                        <Paper style={{padding:15}}>
                            <Skeleton />
                            <Grid container spacing={2}>
                                <Grid item xs={6}><Skeleton height={70}/></Grid>
                                <Grid item xs={6}><Skeleton height={70}/></Grid>
                            </Grid>
                            <Skeleton height={51.44}/>
                        </Paper>
                    </Grid>
                }

                {
                    raffle.ticketCost !== 0 && <Grid item xs={12}>
                        <Paper>
                            {
                                (timeToNextDraw(raffle.nextDraw) > 0) ?
                                <Typography variant="caption" color="primary" style={{
                                    float: "left",
                                    padding: '15px 15px 0 15px'
                                }}>Drawing in {drawingTime}</Typography>
                                : <Typography variant="caption" color="primary" style={{float: "left", padding: '15px 15px 0 15px'}}>Waiting for Winner</Typography>
                            }

                            <Typography variant="caption" color="primary" style={{
                                float: "right",
                                padding: '15px 15px 0 15px'
                            }}>Max Entries: {raffle.entriesAllowed} - Draw# {raffle.raffleID}</Typography>

                            <List>
                                <ListItem>
                                    {
                                        raffle.entriesAllowed > 1 && <ListItemText
                                            primary={getOccurrence(raffle.players, wallet.address)}
                                            primaryTypographyProps={{
                                                style: {fontWeight: 700},
                                                variant: 'h5'
                                            }}
                                            secondary="My Entries"/>
                                    }
                                    <ListItemText
                                        primary={`${CoineusCryptoFormat(raffle.jackpot)} ${raffle.tokens.jackpot.symbol}`}
                                        primaryTypographyProps={{
                                            style: {fontWeight: 700},
                                            variant: 'h5',
                                            textAlign: 'right'
                                        }}
                                        secondary="Current Jackpot"
                                        secondaryTypographyProps={{textAlign: 'right'}}
                                    />
                                </ListItem>

                                <ListItem>
                                    {
                                        checkChain()
                                            ?
                                            <Button
                                                fullWidth
                                                variant="contained"
                                                disabled={getButtonDisabled()}
                                                onClick={enter}
                                            >
                                                {getButtonLabel()}
                                            </Button>
                                            :
                                            <Button
                                                fullWidth
                                                variant="contained"
                                                onClick={changeNetwork}>CHANGE NETWORK</Button>
                                    }
                                </ListItem>

                                {
                                    (timeToNextDraw(raffle.nextDraw) < 0) && <ListItem>
                                        <Button
                                            fullWidth
                                            variant="contained"
                                            onClick={pickWinner}>PICK WINNER</Button>
                                    </ListItem>
                                }

                            </List>
                        </Paper>
                    </Grid>
                }

                <Grid item xs={12}>
                    <Paper>
                        <List>
                            <ListItem>
                                <ListItemText
                                    primary={`0X...${raffle.previousWinner.substr(-6)}`}
                                    secondary="Previous Winner"
                                />
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(raffle.previousJackpot / (10 ** 18))} ${raffle.tokens.jackpot.symbol}`}
                                    primaryTypographyProps={{textAlign: 'right'}}
                                    secondary="Amount"
                                    secondaryTypographyProps={{textAlign: 'right'}}
                                />
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper>
                        <List>
                            <Typography style={{marginLeft: 15}}>All-time Stats</Typography>
                            <ListItem>
                                <ListItemText
                                    primary={raffle.playerStats.entries}
                                    secondary="My Entries"
                                />
                                <ListItemText
                                    primary={raffle.playerStats.wins}
                                    secondary="My Wins"
                                />
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(raffle.playerStats.winnings / (10 ** 18))} ${raffle.tokens.jackpot.symbol}`}
                                    secondary="My Winnings"
                                />
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper style={{padding: 15}}>
                        <List dense disablePadding>
                            {
                                raffle.players.length > 0 && <>
                                    <Typography>{raffle.players.length} Entries</Typography>
                                    {
                                        raffle.players.map((player, i) => {
                                            return <ListItem key={player+""+i} disablePadding>
                                                <ListItemText
                                                    primary={player}
                                                    primaryTypographyProps={{
                                                        color: player.toLowerCase() === wallet.address.toLowerCase() ? "primary" : "",
                                                        variant: "caption",
                                                        fontFamily: 'monospace'
                                                    }}
                                                />
                                            </ListItem>
                                        })
                                    }
                                </>
                            }

                            {
                                raffle.players.length <= 0 && <ListItem>
                                    <ListItemText primary="No Entries"/>
                                </ListItem>
                            }

                        </List>
                    </Paper>
                </Grid>
            </Grid>
            <CoineusLoading open={pending} label="Transaction Pending"/>
        </Container>
    );
}

function timeToNextDraw(d) {
    return d - new Date();
}

function msToHMS(ms) {
    let h = ms / 3.6e6 | 0;
    let m = (ms % 3.6e6) / 6e4 | 0;
    let s = (ms % 6e4) / 1e3 | 0;
    return `${h}:${('' + m).padStart(2, '0')}:${('' + s).padStart(2, '0')}`;
}

function getOccurrence(array, value) {
    return array.filter((v) => (v === value)).length;
}